import React from "react";
import {
    makeStyles,
} from "@material-ui/core";
import {
    NavLink,
} from 'react-router-dom';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';

import HomeIcon from '@material-ui/icons/HomeRounded';
import SettingsIcon from '@material-ui/icons/SettingsRounded';
import AccountIcon from '@material-ui/icons/AccountCircle'
import Logo from '@material-ui/icons/PolymerRounded'
import clsx from "clsx";
const drawerWidth = 65;
const useStyles = makeStyles((theme) => ({
    drawer: {
        width: drawerWidth,
        flexShrink: 0,
      },
      drawerPaper: {
        width: drawerWidth,
      },
      drawerContainer: {
        overflow: 'auto',
      },
      topItems: {
          height: '100%',
          display: 'flex',
          flexDirection: "column",
          justifyContent: 'space-between',
      },
    dFlex:{
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },

    isActive: {
        background: '#607d8b',
        boxShadow: '0 2px 5px rgba(0,0,0,.16), 0 2px 10px rgba(0,0,0,.12)',
        transition: 'all 0.3s ease-in',
        '& svg': {
            color: '#fff !important',
        },
        '&:after': {
            content: "chevron_right",
            fontFamily: 'Material Icons',
            position: 'absolute',
            left: 36,
            top: 0,
            color: '#607d8b',
            fontSize: 2,
        },
        '&:hover': {
            background: '#607d8b !important',
        }
    },

    roundButton: {
        borderRadius: '50%',
        height: 45,
        width: 45,
        '& svg': {
            color: '#607d8b',
        },
    },

    pt13: {
        paddingTop: 13
    },

    pt16: {
        paddingTop: 16
    },

    logo: {
        height: 40,
        marginTop: 4,
        boxShadow: '0 2px 5px rgba(0,0,0,.16), 0 2px 10px rgba(0,0,0,.12)',
        borderRadius: 4,
    }
}));

const SideDrawer = (props) => {
    const classes = useStyles();
    const CustomLink = (props) => {
        return (
            <NavLink
                {...props}
                isActive={(match, location) => {
                    if (!match) {
                      return false;
                    }
                    // only consider an event active if its event id is an odd number
                    // const eventID = parseInt(match.params.eventID);
                    return match.url == location.pathname
                }}
                activeClassName={classes.isActive}
            >{props.children}</NavLink>
        )
    }

    const AdapterLink = React.forwardRef((props, ref) => <CustomLink innerRef={ref} {...props} />);

    
    return (
        <>
            <div className={classes.root}>
            <Drawer
                className={classes.drawer}
                variant="permanent"
                classes={{
                paper: classes.drawerPaper,
                }}
                >
                <div className={classes.topItems}>
                    <div className={classes.drawerContainer}>
                        <ListItem className={classes.dFlex} key={'Home'}>
                        <NavLink
                            to="/">
                                <Logo className={classes.logo}/>
                                {/* <img src={Logo} height="40px" className={classes.logo} /> */}
                        </NavLink>
                        </ListItem>
                        <List className={clsx(classes.dFlex, classes.pt16)}>
                            <ListItem component={AdapterLink} to="/" className={clsx(classes.dFlex, classes.roundButton)} button key={'Home'}>
                                <ListItemIcon className={classes.dFlex} title={'Home'}><HomeIcon /></ListItemIcon>
                            </ListItem>
                        </List>
                        </div>
                        <div className={classes.drawerContainer}>
                            <List className={classes.dFlex}>
                                <ListItem component={AdapterLink} to="/settings" className={clsx(classes.dFlex, classes.roundButton)} button key={'Settings'}>
                                    <ListItemIcon className={classes.dFlex} title={'Settings'}><SettingsIcon /></ListItemIcon>
                                </ListItem>
                            </List>
                            <List className={classes.dFlex}>
                                <ListItem component={AdapterLink} to="/my_profile" className={clsx(classes.dFlex, classes.roundButton)} button key={'Account'}>
                                    <ListItemIcon className={classes.dFlex} title={'Account'}><AccountIcon /></ListItemIcon>
                                </ListItem>
                            </List>
                    </div>
                </div>
            </Drawer>
            
            </div>
        </>
    );
};

export default SideDrawer;
